.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}



.card {
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 15px;
  max-width: 300px;
  width: 100%;
  padding: 20px;
  text-align: center;
 color: rgb(6, 6, 6);

}
.bg{
  display: flex;
          justify-content: center;
          align-items: center;
          height: 100vh;
         
          background-image: url('https://cdn.pixabay.com/photo/2020/08/30/09/28/buildings-5528981_640.jpg');
          background-size: cover;
          background-position: center;
}
.card:hover {
  transform: scale(1.1);
}








